<template>
    <div>
        <div class="wrapper" ref="wrapper">
            <div class="s-content">
                <div class="area">
                <p class="title">当前城市</p>
                <div class="content">
                    <div class="btn">
                       {{$store.state.city}}
                    </div>
                </div>
            </div>
            <div class="area">
                <p class="title2">热门城市</p>
                <div class="content">
                    <div class="btn" @click="handleClick(item.name)" v-for="(item,index) of hotCities" :key="index">{{item.name}}</div>
                </div>
            </div>
            <div class="area" v-for="(item,index) of cities" :key="index">
                <p class="title3" :ref="index">{{index}}</p>
                <div class="list-city" v-for="(city,inx) of item" :key="inx">{{city.name}}</div>
            </div>
            </div>
        
        </div>
    </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
    name:"CityList",
    props:['hotCities','cities','letter'],
    mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
  watch:{
      letter(){
          var elment = this.$refs[this.letter][0];
          this.scroll.scrollToElement(elment)
        //   console.log(this.letter)
      }
  },
  methods:{
      handleClick(city){
          this.$store.commit("changeCity", city)
        // console.log(city);
      }
  }
}
</script>
<style lang="scss" scoped>
.wrapper{
    width:100%;
    height:900px;
    overflow: hidden;
    position: relative;
    margin-top:200px;
}
.title{
    padding:15px;
    background: #eee;
    margin-top:26.333vw;
    text-align: left;
}
.title2{
    padding:15px;
    background: #eee;
    text-align: left;
}
.content{
    height:130px;
    // background: pink;
    display:grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding:20px;
    align-items: center;
    grid-row-gap: 20px;
}
.btn{
    width:26.667vw;
    height:6.667vw;
    line-height: 6.667vw;
    border:1px solid #dfdfdf;
    border-radius: 2vw;
    // margin-left:35px;
}
.title3{
    padding-left:20px;
    line-height: 60px;
    background: #eee;
    text-align: left;
}
.list-city{
    padding-left:20px;
    line-height: 70px;
    border-bottom: 1px solid #ccc;
    text-align: left;

}
</style>
