<template>
    <div>
        <img :src="bannerImg" class="banner" alt="" @click="handleClick">
        <div>
            <detail-nav v-if="isNav" :style="opacitys"></detail-nav>
            <router-link v-else tag="i" to="/" i class="iconfont icon1">&#xe660;</router-link>
        </div>
       <v-fade>
           <detail-gallary slot="fade" v-show="isGallary" @toggle="hide"></detail-gallary>
       </v-fade>
       <detail-list :categoryList="categoryList"></detail-list>
       <div class="content"></div>
    </div>
</template>
<script>
import {DetailModel} from "../../models/DetailModel"
const detailModel =new  DetailModel()
import DetailGallary from "./components/gallary"
import DetailNav from "./components/nabar"
import VFade from './components/Fade';
import DetailList from "./components/List"
export default {
    name:"detail",
    data(){
        return{
            bannerImg:"",
            isGallary:false,
            isNav:false,
            opacitys:{
                opacity:0
            }
        }
    },
    components:{
        DetailGallary,
        DetailNav,
        VFade,
        DetailList
    },
    mounted(){
        window.addEventListener("scroll",this.handleScroll)
        var id = this.$route.params.id;
        detailModel.getDetail(id).then(res=>{
            var data = res.data.data;
            this.bannerImg = data.bannerImg;
            this.gallaryImgs = data.gallaryImgs;
            this.categoryList = data.categoryList;
        });
    },
    destroyed(){
        window.removeEventListener("scroll",this.handleScroll)
    },
    methods:{
        handleScroll(){
            var top = document.documentElement.scrollTop || document.body.scrollTop;
            if(top>0){
                this.isNav = true;
                var opacity = top/200;
                if(opacity>1){
                    opacity = 1;
                }
                this.opacitys.opacity = opacity;
            }else{
                this.isNav = false
            }
        },
        handleClick(){
            this.isGallary = true;
        },
        hide(){
            this.isGallary = false
        }
    }
}
</script>
<style style="scss" scoped>
.content{
    height:2000px;
}
.banner{
    width:100%;
    height:400px;
}
.icon1{
    font-size:50px;
    font-weight: bold;
    position:absolute;
    top:10px;
    left:10px;
    color:#333;
}
</style>
