<template>
  <div >
      <div class="content">
        <input v-model="keyword" type="text" placeholder="输入城市名或拼音" class="search">
      </div>

    <div class="wrapper" ref="wrapper" v-show="keyword">
        <ul class="search-content" >
            <li @click="handleClick(item.name)" class="item" v-for="(item,index) of list" :key="index">{{item.name}}</li>
            <li class="item" v-show="!list.length">没有你想要的城市</li>
        </ul>
    </div>
  </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  name: "CitySearch",
  props: ["cities"],
  data() {
    return {
      keyword: "",
      list: []
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
   methods:{
      handleClick(city){
          this.$store.commit("changeCity", city)
        // console.log(city);
        this.$router.push("/");
        this.keyword=""
      }
  },
  watch: {
    keyword() {
      if (!this.keyword) {
        this.list = [];
        return;
      }
      var result = [];
      for (var i in this.cities) {
        this.cities[i].forEach(ele => {
          if (
            ele.spell.startsWith(this.keyword) ||
            ele.name.startsWith(this.keyword)
          ) {
            result.push(ele);
          }
        });
      }
      this.list = result;
      // console.log(this.cities);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "style/global.scss";
.content {
  z-index: 100;
  position: fixed;
  width: 100%;
  top: 88px;
  height: 14.733vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background: color(bg);
}
.search {
  width: 90%;
  height: 70px;
  border-radius: 15px;
  font-size: 22px;
  padding-left: 15px;
  border-style: none;
}

.item {
  padding-left: 20px;
  line-height: 70px;
  border-bottom: 1px solid #dfdfdf;
  text-align: left;
}
.wrapper {
    z-index: 100;
    background: #fff;
    width:100%;
    height:1000px;
    overflow: hidden;
    position: fixed;
}
</style>
