<template>
  <div class="home">
    <home-head></home-head>
    <home-swiper :swiperList="banner"></home-swiper>
    <home-icons :icons="icons"></home-icons>
    <home-recommend :recommendList="recommendList"></home-recommend>
    <home-weekend :weekendList="weekendList"></home-weekend>
  </div>
</template>

<script>
import HomeHead from './component/Head'
import HomeSwiper from './component/Swiper'
import HomeIcons from './component/Icons'
import {HomeModel} from "../../models/HomeModel"
import HomeRecommend from "./component/Recommend"
import HomeWeekend from "./component/Weekend"
const homeModel = new HomeModel();
export default {
  name: 'home',
  components:{
    HomeHead,
    HomeSwiper,
    HomeIcons,
    HomeRecommend,
    HomeWeekend
  },
  data(){
    return {
      banner:[],
      icons:[],
      recommendList:[],
      weekendList:[],
      currentCity:""
    }
  },
  computed:{
    city(){
      return this.$store.state.city
    }
  },
  activated(){
    if(this.currentCity != this.city){
      this.currentCity = this.city;
      homeModel.getHome(this.city).then(res=>{
      let data = res.data.data;
      let {banner,icons,recommendList,weekendList}=data;
      this.banner = banner;
      this.icons = icons;
      this.recommendList =recommendList;
      this.weekendList = weekendList;
    })
    }
  },
  mounted(){
    this.currentCity = this.city;
    homeModel.getHome(this.city).then(res=>{
      let data = res.data.data;
      let {banner,icons,recommendList,weekendList}=data;
      this.banner = banner;
      this.icons = icons;
      this.recommendList =recommendList;
      this.weekendList = weekendList;
    })
  }
}
</script>

