<template>
      <div class="gallary" @click="handleClick">
        <swiper :options="swiperOption">
            <swiper-slide v-for="item of swiperList" :key="item.id">
                <img class="swiper-img" :src="item.imgUrl" alt="">
            </swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>
<script>
export default {
  name: "DetailGallary",
  data() {
    return {
      swiperOption: {
        pagination: {
          el: ".swiper-pagination",
          type:'fraction'
        },
        /* 是否循环 */
        loop: true,
        autoplay:false,
        observer:true,
        observeParents:true,
      },
      swiperList: [
          {id:'001',imgUrl:"http://img1.qunarzz.com/piao/fusion/1811/b4/ac00a771d74f5a02.jpg_750x200_a71cff60.jpg"},
          {id:'002',imgUrl:"http://img1.qunarzz.com/piao/fusion/1710/15/898d6d74e9b06202.jpg_750x200_89b4509e.jpg"},
          {id:'003',imgUrl:"http://img1.qunarzz.com/piao/fusion/1809/2d/0d7dc3400a50b502.jpg_750x200_4ca95dd4.jpg"}
      ]
    };
  },
  methods:{
      handleClick(){
          this.$emit("toggle")
      }
  }
};
</script>
<style style="scss" scoped>
.gallary{
    position:fixed;
    left:0;
    right:0;
    bottom:0;
    top:0;
    background:#333;
    z-index:1;
}
.swiper-img{
    width:100%;
    height: 300px;
}
.swiper-container{
    top:50%;
    transform:rotateY(-50%);
}
.swiper-pagination{
    bottom:-50px;
    color:#fff;
}
.gallary >>> .swiper-container{
    overflow:inherit;
}
</style>
