<template>
    <div>
        <ul class="word">
            <li @click="handleClick" v-for="(item,index) of cities" :key="index">{{index}}</li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"CityWord",
    props:['cities'],
    methods:{
        handleClick(event){
            // console.log(event.target.innerText)
            this.$emit("changeWord" ,event.target.innerText)
        }
    }
}
</script>
<style lang="scss" scoped>
@import 'style/global.scss';
.word{
    color: #00BCD4;
    position: absolute;
    top:300px;
    right:30px;
}
</style>
