import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    city:"武汉"
  },
  mutations: {
    changeCity(state,city){
      try{
        localStorage.setItem("city",city)
      }catch(err){
        console.log(err)
      }
      state.city = city;
    }
  },
  actions: {

  },
  getters:{
    doubleCity(state){
      return"城市："+state.city
    }
  }
})
